import React, { useState } from 'react';
import 'css/default.css';
import 'css/comment/CommentBox.css';
import { useDispatch } from 'react-redux';
import { deleteComment } from 'redux/action/commentAction';
import CommentModalBox from 'comment/component/CommentModalBox';
import CommentReplyBox from 'comment/component/CommentReplyBox';
import { type_comment, type_reply_set } from 'redux/reducer/commentReducer';

// interface type_reply_set{
// }
// content: "test"
// create_date: "2021-05-27T11:09:21.348535+09:00"
// id: 1
// nickname: "test"
// reply_set: []
// update_date: "2021-05-27T11:09:21.348611+09:00"
//   
function CommentBox({nickname, create_date, content, id, reply_set}: type_comment){
    const dispatch = useDispatch();
    const [isShowModal, setIsShowModal] = useState<boolean>(false);         //     
    const [modalType, setModalType] = useState(0);             //     ,  0 : , 1 : , 2 : 
    const [isReply, setIsReply] = useState<boolean>(false);             //     ,  false : , true : 

    //     
    const onModifyHandler = () => {
        setIsShowModal(true);
        setModalType(1);
        setIsReply(false);

    }

    //     
    const onDeleteHandler = (id:number) => {
        setIsShowModal(true);
        setModalType(0);
        setIsReply(false);

    }

    const onReplyHandler = (id:number) => {
        setIsShowModal(true);
        setIsReply(true);
    }



    const replyDummy = () => {
        let tmp = [];
        for(let i = 0; i < 2; i++){
            tmp.push({
                nick:`ttt${i}`,
                date:"2020.01.01",
                comment:"aaaaaaaaaaaaaaaaaaaaa" + i.toString(),
                id: i,
            })
        }
        return tmp;
    }

    return (
    <>
        {isShowModal && <CommentModalBox type={modalType} setIsShowModal={setIsShowModal} id={id} content={content} nickname={nickname} isReply={isReply}/>}
        <section className={'commentbox'}>
            <div className={'commentbox__first__line'}>
                <div>
                    <h2>{nickname}</h2>
                    <p>{create_date.slice(0, 10)}</p>
                </div>
                
                <div className={'commentbox__btn__list'}>
                    <button onClick={onModifyHandler}></button>
                    <div>/</div>
                    <button onClick={()=>onDeleteHandler(id)}></button>
                    <button onClick={()=>onReplyHandler(id)}> </button>
                </div>
            </div>

            <p>{content}</p>
            <section >
                {reply_set.map((value:type_reply_set, index:number) => (<CommentReplyBox key={index} p_id={id} reply_set={value} />) )}
            </section>

        </section>
    </>
    );
}
export default CommentBox;