import React, {useState} from 'react';
import 'css/default.css';
import 'css/comment/CommentForm.css';
import { useDispatch } from 'react-redux';
import { addComment, getComment } from 'redux/action/commentAction';

//   
function CommentForm(){
    const [nickname, setNickname] = useState<string>("");
    const [password, setPassword] = useState<string>("");
    const [content, setContent] = useState<string>("");
    const dispatch = useDispatch();

    //   
    const onCommentAddHandler = (e:any) => {
        e.preventDefault();

        if(nickname === "") return alert(" ");
        if(nickname.length > 7) return alert(" 7   .");

        if(content === "") return alert(" ");
        if(content.length > 121) return alert(" 120   .");
        if(password === "") return alert("  ");
        if(password.length > 7) return alert(" 7   .");
        
        const data = new FormData();
        data.append('nickname', nickname);
        data.append('password', password);
        data.append('content', content);

        const tmp = addComment(data);
        tmp(dispatch).then((res)=>{
            if(res?.status === 201){
                let getCommentApi = getComment(1);
                getCommentApi(dispatch);
            }
            
        });

        setNickname("");
        setContent("");
        setPassword("");
    }

    return (
    <section>
        <form className={'comment__form'}>
            <div className={'first__form__line'}>
                <div>
                    <input maxLength={7} placeholder={''} value={nickname} onChange={(e)=>setNickname(e.target.value)}/>
                    <input maxLength={7} placeholder={''} value={password} type="password" onChange={(e)=>setPassword(e.target.value)}/>
                </div>
                <button className={'comment__form__submit'} type="submit" onClick={(e)=>onCommentAddHandler(e)}></button>
            </div>
            <textarea placeholder={'         .'} value={content} onChange={(e)=>setContent(e.target.value)}></textarea>
        </form>
    </section>);
}
export default CommentForm;